<template>
  <div class="manage-index">
    <!---->
    <el-row>
      <el-col :span="24" class="toolbar">
        <el-form :inline="true" :model="filters" ref="filters" size="small">
          <el-form-item label="" prop="name">
            <el-input v-model="filters.name" placeholder="" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="year" class="ybox">
            <el-date-picker v-model="filters.year" type="year" value-format="yyyy" placeholder=""></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="leadleaderid" class="ld">
            <el-select v-model="filters.leadleaderid" filterable clearable placeholder="">
              <el-option
                v-for="item in leaderList"
                :key="item.id"
                :label="item.name"
                :value="item.id"
                @change="selectChange"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button @click="getDatas('filters')" type="primary"> </el-button>
            <el-button @click="resetForm('filters')"> </el-button>
          </el-form-item>
          <div class="toolbtns">
            <el-button type="primary" @click="handleAdd" size="small" v-if="isAddShow"> </el-button>
          </div>
        </el-form>
      </el-col>
    </el-row>
    <!--  -->
    <el-row>
      <el-table
        class="tableinfo"
        border
        :height="tableHeight"
        :data="tableData"
        highlight-current-row
        style="width: 100%;"
      >
        <el-table-column label="" width="100" :formatter="numberFormatter"></el-table-column>
        <el-table-column prop="year" width="100" label=""></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="type" label="">
          <template slot-scope="scope">
            <span v-html="typeFormat(scope,scope.row.type)"></span>
          </template>
        </el-table-column>
        <el-table-column prop="unit" label=""></el-table-column>
        <el-table-column prop="leadleadername" label=""></el-table-column>
        <el-table-column prop="dytarget" label=""></el-table-column>
        <el-table-column prop="remark" label=""></el-table-column>
        <el-table-column prop="formstatus" label="">
          <template slot-scope="scope">
            <div>
              <i class="fa mark" v-if="scope.row.formstatus==1" style="background:#fbe804;"></i>
              <i class="fa mark" v-else-if="scope.row.formstatus==2" style="background:#f00;"></i>
              <i class="fa mark" v-else-if="scope.row.formstatus== -1" style="background:#999;"></i>
              <i class="fa mark" v-else></i>
              <span v-html="formstatusFormat(scope,scope.row.formstatus)"></span>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="totalstatus" label="">
          <template slot-scope="scope">
            <div class="flexbox" v-if="isEditShow">
              <i class="fa mark" v-if="scope.row.totalstatus==3" style="background:#364e98;"></i>
              <i class="fa mark" v-else-if="scope.row.totalstatus==4" style="background:#7c1d77;"></i>
              <i class="fa mark" v-else-if="scope.row.totalstatus== -1" style="background:#999;"></i>
              <i class="fa mark" v-else></i>
              <el-select
                v-model="scope.row.totalstatus"
                @change="totalstateChange(scope, scope.row)"
                class="stateSelect"
              >
                <el-option
                  v-for="item in totalStatus"
                  :key="item.value"
                  :label="item.status"
                  :value="item.value"
                ></el-option>
              </el-select>
            </div>
            <div v-else>
              <i class="fa mark" v-if="scope.row.totalstatus==3" style="background:#364e98;"></i>
              <i class="fa mark" v-else-if="scope.row.totalstatus==4" style="background:#7c1d77;"></i>
              <i class="fa mark" v-else-if="scope.row.totalstatus== -1" style="background:#999;"></i>
              <i class="fa mark" v-else></i>
              <span v-html="totalstatusFormat(scope,scope.row.totalstatus)"></span>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template scope="scope">
            <el-button class="btn" @click="handleLook(scope.$index, scope.row)"></el-button>
            <el-button class="btn" @click="handleEdit(scope.$index, scope.row)" v-if="isEditShow"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 40, 50]"
        :page-size="pageSize"
        layout="total, prev, pager, next, sizes, jumper"
        :total="totals"
      ></el-pagination>
    </el-row>
    <!--  -->
    <el-dialog class="managebox" :title="formTitle" :visible.sync="addFormVisible" width="1200px">
      <el-row class="addrow">
        <el-col :span="24">
          <fieldset>
            <legend></legend>
            <div class="boxcontent" style="padding-left:0;">
              <el-form :model="addForm" label-width="140px" ref="addForm" size="small">
                <el-form-item
                  label=""
                  prop="name"
                  :rules="[{ required: true, message: ''}]"
                >
                  <el-input v-model="addForm.name"></el-input>
                </el-form-item>
                <div class="flexbox">
                  <el-form-item
                    label=""
                    prop="year"
                    :rules="[{ required: true, message: ''}]"
                  >
                    <el-date-picker
                      v-model="addForm.year"
                      type="year"
                      value-format="yyyy"
                      placeholder=""
                      @change="selectChange"
                    ></el-date-picker>
                  </el-form-item>
                  <el-form-item label="" prop="unit">
                    <el-input v-model="addForm.unit"></el-input>
                  </el-form-item>
                  <el-form-item
                    label=""
                    prop="type"
                    :rules="[{ required: true, message: ''}]"
                  >
                    <el-radio v-model="addForm.type" label="0"></el-radio>
                    <el-radio v-model="addForm.type" label="1"></el-radio>
                  </el-form-item>
                </div>
                <div class="flexbox">
                  <el-form-item label="" prop="leadleaderid">
                    <el-select
                      v-model="addForm.leadleaderid"
                      placeholder=""
                      filterable
                      clearable
                      @change="selectChange"
                    >
                      <el-option
                        v-for="item in leaderList"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="" prop="cytarget">
                    <el-input v-model="addForm.cytarget" placeholder=""></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="dytarget">
                    <el-input placeholder="" v-model="addForm.dytarget"></el-input>
                  </el-form-item>
                </div>
                <el-form-item label="" prop="remark" class="remark">
                  <el-input type="textarea" :autosize="{ minRows: 3}" v-model="addForm.remark"></el-input>
                </el-form-item>
              </el-form>
            </div>
          </fieldset>
        </el-col>
      </el-row>
      <el-row :gutter="20" class="index-leader">
        <el-col :span="12">
          <fieldset>
            <legend></legend>
            <div class="boxcontent">
              <el-button class="addbtn" @click="addLead" size="small" type="primary"> </el-button>
              <el-table
                height="200px"
                border
                :data="leadsData"
                highlight-current-row
                class="tableinfo"
              >
                <el-table-column prop="deptName" label=""></el-table-column>
                <el-table-column prop="leadername" label=""></el-table-column>
                <el-table-column prop="agentname" label=""></el-table-column>
                <el-table-column prop="formstatus" label="" v-if="isStateShow">
                  <template slot-scope="scope">
                    <div>
                      <el-tag size="small">
                        <i
                          class="fa mark"
                          v-if="scope.row.formstatus==1"
                          style="background:#fbe804;"
                        ></i>
                        <i
                          class="fa mark"
                          v-else-if="scope.row.formstatus==2"
                          style="background:#f00;"
                        ></i>
                        <i
                          class="fa mark"
                          v-else-if="scope.row.formstatus== -1"
                          style="background:#999;"
                        ></i>
                        <i class="fa mark" v-else></i>
                        <span v-html="formstatusFormat(scope,scope.row.formstatus)"></span>
                      </el-tag>
                    </div>
                  </template>
                </el-table-column>
                <el-table-column label="">
                  <template scope="scope">
                    <el-button class="btn" @click="handleEdit2(scope.$index, scope.row)"></el-button>
                    <el-button class="btn" @click="handleDelete2(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </fieldset>
        </el-col>
        <el-col :span="12">
          <fieldset>
            <legend></legend>
            <div class="boxcontent">
              <el-button class="addbtn" @click="addRuleBtn" size="small" type="primary"> </el-button>
              <el-table
                border
                :data="ruleList"
                highlight-current-row
                style="width: 100%;"
                class="tableinfo"
                height="200px"
              >
                <el-table-column prop="stagename" label=""></el-table-column>
                <el-table-column prop="cycletype" label="">
                  <template slot-scope="scope">
                    <p v-if="scope.row.cycletype==3"></p>
                    <p v-else-if="scope.row.cycletype==4"></p>
                  </template>
                </el-table-column>
                <el-table-column prop="warnday" label="()"></el-table-column>
                <el-table-column prop="endtime" label=""></el-table-column>
                <el-table-column label="">
                  <template scope="scope">
                    <el-button class="btn" @click="editRuleBtn(scope.$index, scope.row)"></el-button>
                    <el-button class="btn" @click="delRuleBtn(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </fieldset>
        </el-col>
      </el-row>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" size="small" @click="saveSubmit('addForm')">{{btnWord}}</el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog :title="ruleTitle" :visible.sync="ruleVisible" width="420px" class="rule-dialog">
      <el-form :model="ruleForm" label-width="100px" ref="ruleForm" size="small">
        <el-form-item label="" prop="stagename">
          <el-input v-model="ruleForm.stagename" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="condition">
          <el-select v-model="condition" placeholder="" @change="cycleChange()">
            <el-option
              v-for="item in warnlist"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="date2">
          <el-select v-model="date2" clearable placeholder="" @change="selectChange">
            <el-option
              v-for="item in timeday"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" class="day" prop="warnday">
          <el-input v-model="ruleForm.warnday">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item
          label=""
          prop="starttime"
          :rules="[{ required: true, message: '', trigger: 'change' }]"
        >
          <el-date-picker
            v-model="ruleForm.starttime"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            format="yyyy-MM-dd"
            @change="selectChange"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label=""
          prop="endtime"
          :rules="[{ required: true, message: '', trigger: 'change' }]"
        >
          <el-date-picker
            v-model="ruleForm.endtime"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            format="yyyy-MM-dd"
            @change="selectChange"
          ></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitRule" type="primary">{{ruleBtn}}</el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="detailRuleVisible" class="rule-dialog" width="420px">
      <div>
        <el-form :model="ruledetailForm" label-width="120px" ref="ruledetailForm" size="small">
          <el-form-item label="" prop="stagename">
            <el-input readonly v-model="ruledetailForm.stagename"></el-input>
          </el-form-item>
          <el-form-item label="" prop="dcondition">
            <el-input readonly v-model="dcondition"></el-input>
          </el-form-item>
          <el-form-item label="" prop="date2">
            <el-input readonly v-model="date2"></el-input>
          </el-form-item>
          <el-form-item label="" class="day" prop="warnday">
            <el-input readonly v-model="ruledetailForm.warnday">
              <template slot="append"></template>
            </el-input>
          </el-form-item>
          <el-form-item label="" prop="starttime">
            <el-date-picker
              v-model="ruledetailForm.starttime"
              type="date"
              readonly
              placeholder=""
              value-format="yyyy-MM-dd"
              format="yyyy-MM-dd"
              @change="selectChange"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="endtime">
            <el-date-picker
              v-model="ruledetailForm.endtime"
              type="date"
              readonly
              placeholder=""
              value-format="yyyy-MM-dd"
              format="yyyy-MM-dd"
              @change="selectChange"
            ></el-date-picker>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="detailRuleVisible=false" type="primary"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog class="managebox" title="" :visible.sync="detailFormVisible" width="1200px">
      <el-row class="addrow">
        <el-col :span="24">
          <fieldset>
            <legend></legend>
            <div class="boxcontent" style="padding-left:0;">
              <el-form :model="detailForm" label-width="140px" ref="detailForm" size="small">
                <el-form-item label="" prop="name">
                  <el-input v-model="detailForm.name" readonly></el-input>
                </el-form-item>
                <div class="flexbox">
                  <el-form-item label="" prop="year">
                    <el-input v-model="detailForm.year" readonly></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="unit">
                    <el-input v-model="detailForm.unit" readonly></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="type">
                    <el-input v-model="detailForm.type" readonly></el-input>
                  </el-form-item>
                </div>
                <div class="flexbox">
                  <el-form-item label="" prop="leadername">
                    <el-input v-model="detailForm.leadername" readonly></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="cytarget">
                    <el-input v-model="detailForm.cytarget" readonly></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="dytarget">
                    <el-input readonly v-model="detailForm.dytarget"></el-input>
                  </el-form-item>
                </div>
                <el-form-item label="" prop="remark" class="remark">
                  <el-input
                    type="textarea"
                    :autosize="{ minRows: 3}"
                    readonly
                    v-model="detailForm.remark"
                  ></el-input>
                </el-form-item>
              </el-form>
            </div>
          </fieldset>
        </el-col>
      </el-row>
      <el-row :gutter="20" class="index-leader">
        <el-col :span="12">
          <fieldset>
            <legend></legend>
            <div class="boxcontent">
              <el-table
                border
                :data="leadsData"
                height="200px"
                highlight-current-row
                class="tableinfo"
              >
                <el-table-column prop="deptName" label=""></el-table-column>
                <el-table-column prop="leadername" label=""></el-table-column>
                <el-table-column prop="agentname" label=""></el-table-column>
                <el-table-column prop="formstatus" label="">
                  <template slot-scope="scope">
                    <div>
                      <el-tag size="small">
                        <i
                          class="fa mark"
                          v-if="scope.row.formstatus==1"
                          style="background:#fbe804;"
                        ></i>
                        <i
                          class="fa mark"
                          v-else-if="scope.row.formstatus==2"
                          style="background:#f00;"
                        ></i>
                        <i
                          class="fa mark"
                          v-else-if="scope.row.formstatus== -1"
                          style="background:#999;"
                        ></i>
                        <i class="fa mark" v-else></i>
                        <span v-html="formstatusFormat(scope,scope.row.formstatus)"></span>
                      </el-tag>
                    </div>
                  </template>
                </el-table-column>
                <el-table-column label="">
                  <template scope="scope">
                    <el-button class="btn" @click="handleDetail2(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </fieldset>
        </el-col>
        <el-col :span="12">
          <fieldset>
            <legend></legend>
            <div class="boxcontent">
              <el-table
                border
                :data="ruleList"
                highlight-current-row
                style="width: 100%;"
                class="tableinfo"
                height="200px"
              >
                <el-table-column prop="stagename" label=""></el-table-column>
                <el-table-column prop="cycletype" label="" width="80">
                  <template slot-scope="scope">
                    <p v-if="scope.row.cycletype==3"></p>
                    <p v-else-if="scope.row.cycletype==4"></p>
                  </template>
                </el-table-column>
                <el-table-column prop="warnday" label="()"></el-table-column>
                <el-table-column prop="endtime" label=""></el-table-column>
                <el-table-column prop="status" label="">
                  <template slot-scope="scope">
                    <div>
                      <el-tag size="small" v-if="scope.row.status==1">
                        <i class="fa mark" style="background:#364e98;"></i>
                        <span></span>
                      </el-tag>
                      <el-tag size="small" v-else-if="scope.row.status==2">
                        <i class="fa mark"></i>
                        <span></span>
                      </el-tag>
                      <el-tag size="small" v-else-if="scope.row.status==0">
                        <i class="fa mark" style="background:#999;"></i>
                        <span></span>
                      </el-tag>
                      <p v-else>/</p>
                    </div>
                  </template>
                </el-table-column>
                <el-table-column label="" width="60">
                  <template scope="scope">
                    <el-button class="btn" @click="detailRuleBtn(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </fieldset>
        </el-col>
      </el-row>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" size="small" @click="detailFormVisible = false"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="taskTitle"
      :visible.sync="addLeadVisible"
      width="800px"
      class="task-dialog task-dialog2"
    >
      <el-form
        :inline="true"
        :model="addLeadData"
        label-width="100px"
        ref="addLeadData"
        :append-to-body="true"
      >
        <div class="leabox">
          <el-form-item
            label=""
            prop="deptid"
            :rules="[{ required: true, message: '', trigger: 'change' }]"
          >
            <el-select
              v-model="addLeadData.deptid"
              clearable
              placeholder=""
              @change="deptChange"
            >
              <el-option
                v-for="item in deptList"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </div>
        <el-form-item label="" prop="leadername">
          <el-input v-model="addLeadData.leadername"></el-input>
        </el-form-item>
        <el-form-item label="" prop="leaderphone">
          <el-input v-model="addLeadData.leaderphone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentname">
          <el-input v-model="addLeadData.agentname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentphone">
          <el-input v-model="addLeadData.agentphone"></el-input>
        </el-form-item>
        <div class="leabox leabox2">
          <el-form-item label="" prop="duty">
            <el-input type="textarea" :autosize="{ minRows: 10}" v-model="addLeadData.duty"></el-input>
          </el-form-item>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitLead" type="primary">{{changeWord}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  addEconomic,
  allIndexlist,
  indexById,
  editIndex,
  getFilterIndexlist,
  changeFormState,
  changeTotalState
} from "../../api/targetApi";
export default {
  name: "target",
  props: ["leaderList", "deptList"],
  data() {
    return {
      ruledetailForm: {},
      detailRuleVisible: false,
      ruleIndex: -1,
      checkAll: false,
      checkedItems: [],
      allitems: [],
      isIndeterminate: false,
      choosenum: 0,
      hasitem: [],
      choosenum: 0,
      ruleTitle: "",
      ruleVisible: false,
      ruleBtn: "",
      isStateShow: false,
      taskTitle: "",
      changeWord: "",
      changeNum: "",
      readOn: false,
      leadsData: [], //
      addLeadVisible: false,
      addLeadData: {},
      warnlist: [{ value: 4, name: "" }, { value: 3, name: "" }],
      condition: "",
      filters: {},
      addFormVisible: false,
      detailFormVisible: false,
      tableData: [],
      addForm: {},
      currentPage: 1,
      pageSize: 20,
      totals: 0,
      detailForm: {},
      newyear: "",
      formTitle: "",
      btnWord: "",
      change: "",
      tableData: [],
      targetCheckList: JSON.parse(this.$store.getters.userdata).targetCheckList,
      isAddShow: false,
      isEditShow: false,
      tableHeight: "500px",
      curyear: "",
      totalStatus: [
        { value: 0, status: "" },
        { value: 3, status: "" },
        { value: 4, status: "" },
        { value: -1, status: "" }
      ],
      formStatus: [
        { value: 0, status: "" },
        { value: 1, status: "" },
        { value: 2, status: "" },
        { value: -1, status: "" }
      ],
      dateshow1: true,
      dateshow1: false,
      // 
      timeday: [
        { value: "1", name: "1" },
        { value: "2", name: "2" },
        { value: "3", name: "3" },
        { value: "4", name: "4" },
        { value: "5", name: "5" },
        { value: "6", name: "6" },
        { value: "7", name: "7" },
        { value: "8", name: "8" },
        { value: "9", name: "9" },
        { value: "10", name: "10" },
        { value: "11", name: "11" },
        { value: "12", name: "12" },
        { value: "13", name: "13" },
        { value: "14", name: "14" },
        { value: "15", name: "15" },
        { value: "16", name: "16" },
        { value: "17", name: "17" },
        { value: "18", name: "18" },
        { value: "19", name: "19" },
        { value: "20", name: "20" },
        { value: "21", name: "21" },
        { value: "22", name: "22" },
        { value: "23", name: "23" },
        { value: "24", name: "24" },
        { value: "25", name: "25" },
        { value: "26", name: "26" },
        { value: "27", name: "27" },
        { value: "28", name: "28" },
        { value: "29", name: "29" },
        { value: "30", name: "30" },
        { value: "31", name: "31" }
      ],
      dateshow1: true,
      dateshow2: false,
      cycleNum: 0,
      date1: "",
      date2: "",
      dcondition: "",
      ruleForm: {},
      ruleList: []
    };
  },
  mounted() {},
  created() {
    this.browerHeight();
    if (this.targetCheckList !== null) {
      for (var i = 0; i < this.targetCheckList.length; i++) {
        let value = this.targetCheckList[i];
        switch (value) {
          case "D-add":
            this.isAddShow = true;
            break;
          case "D-edit":
            this.isEditShow = true;
            break;
        }
      }
    }
    this.getAlldata();
  },
  methods: {
    // 
    stateFormat(scope, value) {
      switch (value) {
        case 2:
          return "";
          break;
        case 1:
          return "";
          break;
        case 0:
          return "";
          break;
      }
    },
    // 
    handleCheckAllChange(val) {
      let arr = [];
      for (var i = 0; i < this.allitems.length; i++) {
        arr.push(this.allitems[i].id);
      }
      this.choosenum = val ? arr.length : 0;
      this.checkedItems = val ? arr : [];
      this.isIndeterminate = false;
    },
    handleCheckedItemsChange(value) {
      this.choosenum = value.length;
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.allitems.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.allitems.length;
    },
    //  end
    selectChange() {
      this.$forceUpdate();
    },
    // 
    // 
    addRuleBtn() {
      this.ruleVisible = true;
      this.ruleTitle = "";
      this.ruleBtn = "";
      this.changeNum = "add";
      this.hasitem = [];
      this.checkedItems = [];
      this.ruleForm = {};
      this.condition = "";
      this.date2 = "";
    },
    // 
    editRuleBtn(index, row) {
      this.hasitem = [];
      this.ruleVisible = true;
      this.ruleTitle = "";
      this.ruleBtn = "";
      this.changeNum = "edit";
      this.ruleForm = Object.assign({}, row);
      this.condition = row.cycletype;
      this.date2 = String(row.cycletime);
      this.ruleIndex = index;
    },
    // 
    detailRuleBtn(index, row) {
      this.detailRuleVisible = true;
      this.ruledetailForm = Object.assign({}, row);
      if (row.cycletype == 4) {
        this.dcondition = "";
      } else if (row.cycletype == 3) {
        this.dcondition = "";
      } else {
        this.dcondition = "";
      }
      if (row.cycletime == null) {
        this.date2 = "";
      } else {
        this.date2 = row.cycletime + "";
      }
    },
    // 
    delRuleBtn(index, row) {
      this.ruleList.splice(index, 1);
      this.ruleVisible = false;
      this.$message({
        message: "",
        type: "success"
      });
    },
    // 
    submitRule() {
      if (this.changeNum == "add") {
        this.addRuleSubmit();
      } else {
        this.editRuleSubmit();
      }
    },
    // 
    addRuleSubmit() {
      // 
      // this.hasitem = [2, 3];
      // var array1 = this.checkedItems;
      // var array2 = this.hasitem;
      // var tempArray1 = [];
      // var tempArray2 = [];
      // for (var m = 0; m < array2.length; m++) {
      //   tempArray1[array2[m]] = true; //array2 tempArray1 true
      // }
      // for (var i = 0; i < array1.length; i++) {
      //   if (!tempArray1[array1[i]]) {
      //     tempArray2.push(array1[i]); //array1 array2 
      //   }
      // }
      // this.checkedItems = tempArray2;

      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          this.ruleForm.cycletype = this.condition;
          this.ruleForm.cycletime = this.date2;
          this.ruleList.push(this.ruleForm);
          this.ruleVisible = false;
        } else {
          return false;
        }
      });
    },
    // 
    editRuleSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.ruleForm);
          para.cycletype = this.condition;
          para.cycletime = this.date2;
          this.ruleList.splice(this.ruleIndex, 1, para);
          this.ruleVisible = false;
        } else {
          return false;
        }
      });
    },
    // end
    // 
    addLead: function() {
      this.changeNum = "add";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = false;
      this.addLeadVisible = true;
    },
    // 
    handleDetail2(index, row) {
      this.changeNum = "read";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = true;
      this.addLeadVisible = true;
      this.addLeadData = Object.assign({}, row);
    },
    // 
    handleEdit2(index, row) {
      this.changeNum = "edit";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = false;
      this.addLeadVisible = true;
      this.addLeadData = Object.assign({}, row);
      this.tindex = index;
    },
    // -
    submitLead: function() {
      if (this.changeNum == "read") {
        this.addLeadVisible = false;
      } else if (this.changeNum == "add") {
        this.addLeadSubmit();
      } else {
        this.editLeadSubmit();
      }
    },
    deptChange() {
      let deptname = "";
      for (var i = 0; i < this.deptList.length; i++) {
        if (this.deptList[i].id == this.addLeadData.deptid) {
          deptname = this.deptList[i].name;
          break;
        }
      }
      this.addLeadData.deptName = deptname;
    },
    // 
    addLeadSubmit: function() {
      this.$refs.addLeadData.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.addLeadData);
          this.leadsData.push(para);
          this.$refs["addLeadData"].resetFields();
          this.addLeadVisible = false;
        } else {
          return false;
        }
      });
    },
    // 
    editLeadSubmit: function() {
      this.$refs.addLeadData.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.addLeadData);
          this.leadsData.splice(this.tindex, 1, para);
          this.$refs["addLeadData"].resetFields();
          this.addLeadVisible = false;
        } else {
          console.log("error");
          return false;
        }
      });
    },
    // 
    handleDelete2(index, row) {
      this.leadsData.splice(index, 1);
      this.$refs["addLeadData"].resetFields();
      this.addLeadVisible = false;
      this.$message({
        message: "",
        type: "success"
      });
    },

    // 
    cycleChange() {
      this.$forceUpdate();
      this.cycleNum = this.condition;
    },
    //  - 
    totalstateChange(scope, row) {
      let para = { totalstatus: row.totalstatus, id: row.id };
      changeTotalState(para, data => {
        this.getAlldata();
        this.$message({
          type: "success",
          message: "!"
        });
      });
    },
    //  - 
    formstateChange(scope, row) {
      let para = { formstatus: row.formstatus, id: row.id };
      changeFormState(para, data => {
        this.getAlldata();
        this.$message({
          type: "success",
          message: "!"
        });
      });
    },
    // 
    totalstatusFormat(scope, value) {
      switch (value) {
        case 0:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case -1:
          return "";
          break;
      }
    },
    // 
    formstatusFormat(scope, value) {
      switch (value) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case -1:
          return "";
          break;
      }
    },
    // 
    numberFormatter(row, column, cellValue, index) {
      return index + 1 + (this.currentPage - 1) * this.pageSize;
    },
    // 
    typeFormat(scope, value) {
      switch (value) {
        case 1:
          return "";
          break;
        case 0:
          return "";
          break;
      }
    },
    // table
    browerHeight() {
      var bheight = document.body.clientHeight;
      var hh = bheight - 380;
      this.tableHeight = hh + "px";
    },
    // 
    getAlldata() {
      let para = { pageNum: this.currentPage, pageSize: this.pageSize };
      getFilterIndexlist(para, data => {
        this.tableData = data.data.obj.datas;
        this.totals = data.data.obj.total;
      });
    },
    timestampToTime(row, column) {
      var date = new Date(row.startyear); //10*1000131000
      var Y = date.getFullYear();
      var M =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var D = date.getDate() + " ";
      return Y;
    },
    timestampToTime2(row, column) {
      var date = new Date(row.endyear); //10*1000131000
      var Y = date.getFullYear() + "-";
      var M =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var D = date.getDate() + " ";
      return Y + M;
    },
    // 
    getDatas: function() {
      let para = this.filters;
      para.pageNum = this.currentPage;
      para.pageSize = this.pageSize;
      getFilterIndexlist(para, data => {
        this.tableData = data.data.obj.datas;
        this.totals = data.data.obj.total;
      });
    },
    selectChange() {
      this.$forceUpdate();
    },

    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.getAlldata();
    },
    // 
    handleSizeChange: function(size) {
      this.pageSize = size;
      this.getAlldata();
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
      this.getAlldata();
    },
    getcurYear() {
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      this.curyear = year + "-" + month + "-" + strDate;
      return this.curyear;
    },
    // 
    handleAdd() {
      this.date2 = "";
      this.isStateShow = false;
      this.getcurYear();
      this.addFormVisible = true;
      this.formTitle = "";
      this.btnWord = "";
      this.change = "add";
      this.leadsData = [];
      this.ruleForm = {};
      this.ruleList = [];
      this.addForm = {
        type: "0",
        year: this.curyear
      };
    },
    // 
    handleLook(index, row) {
      this.leadsData = [];
      this.date2 = "";
      let para = { id: row.id };
      this.ruleForm = {};
      this.ruleList = [];
      this.detailFormVisible = true;
      indexById(para, data => {
        this.detailForm = data.data.obj;
        this.detailForm.leadername = row.leadleadername;
        if (this.detailForm.type == 0) {
          this.detailForm.type = "";
        } else {
          this.detailForm.type = "";
        }
        if (data.data.obj.leaderList == null) {
          data.data.obj.leaderList = [];
        }
        this.leadsData = data.data.obj.leaderList;
        // 
        if (
          data.data.obj.ruleList == null ||
          data.data.obj.ruleList.length == 0
        ) {
          this.ruleList = [];
        } else {
          this.ruleList = data.data.obj.ruleList;
          for (var n = 0; n < this.ruleList.length; n++) {
            // 
            var sdate = this.ruleList[n].starttime;
            var date = new Date(sdate);
            var Y = date.getFullYear() + "-";
            var M =
              (date.getMonth() + 1 < 10
                ? "0" + (date.getMonth() + 1)
                : date.getMonth() + 1) + "-";
            var D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
            this.ruleList[n].starttime = Y + M + D;
            // 
            var edate = this.ruleList[n].endtime;
            var date2 = new Date(edate);
            var Y2 = date2.getFullYear() + "-";
            var M2 =
              (date2.getMonth() + 1 < 10
                ? "0" + (date2.getMonth() + 1)
                : date2.getMonth() + 1) + "-";
            var D2 =
              date2.getDate() < 10 ? "0" + date2.getDate() : date2.getDate();
            this.ruleList[n].endtime = Y2 + M2 + D2;
          }
        }
      });
    },
    // 
    handleEdit(index, row) {
      this.addForm = {};
      this.date1 = "";
      this.date2 = "";
      this.isStateShow = true;
      this.leadsData = [];
      this.ruleForm = {};
      this.ruleList = [];
      let para = { id: row.id, ishand: 1, formstatus: row.formstatus };
      indexById(para, data => {
        var datas = data.data.obj;
        if (datas.year == null) {
          datas.year = "";
        } else {
          datas.year = datas.year + "-01-01";
        }
        this.addForm = datas;
        this.addForm.id = row.id;
        if (datas.ischeck == 1) {
          this.addForm.ischeck = true;
        } else {
          this.addForm.ischeck = false;
        }
        this.addForm.type = String(row.type);
        // 
        if (datas.ruleList == null || datas.ruleList.length == 0) {
          this.ruleList = [];
        } else {
          this.ruleList = datas.ruleList;
          for (var n = 0; n < this.ruleList.length; n++) {
            // 
            var sdate = this.ruleList[n].starttime;
            var date = new Date(sdate);
            var Y = date.getFullYear() + "-";
            var M =
              (date.getMonth() + 1 < 10
                ? "0" + (date.getMonth() + 1)
                : date.getMonth() + 1) + "-";
            var D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
            this.ruleList[n].starttime = Y + M + D;
            // 
            var edate = this.ruleList[n].endtime;
            var date2 = new Date(edate);
            var Y2 = date2.getFullYear() + "-";
            var M2 =
              (date2.getMonth() + 1 < 10
                ? "0" + (date2.getMonth() + 1)
                : date2.getMonth() + 1) + "-";
            var D2 =
              date2.getDate() < 10 ? "0" + date2.getDate() : date2.getDate();
            this.ruleList[n].endtime = Y2 + M2 + D2;
          }
        }
        if (data.data.obj.leaderList == null) {
          data.data.obj.leaderList = [];
        }
        this.leadsData = data.data.obj.leaderList;
        this.formTitle = "";
        this.btnWord = "";
        this.addFormVisible = true;
        this.change = "edit";
      });
    },
    saveSubmit() {
      if (this.change == "add") {
        this.addSubmit();
      } else {
        this.editSubmit();
      }
    },
    // 
    addSubmit() {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          if (this.addForm.ischeck == true) {
            this.addForm.ischeck = 1;
          } else {
            this.addForm.ischeck = 0;
          }
          var y = this.addForm.year;
          var n = y.slice(0, 4);
          this.addForm.year = n;
          let para = Object.assign({}, this.addForm);
          para.ishand = 0;
          para.formstatus = 0;
          para.totalstatus = 0;
          para.leaderList = this.leadsData;
          para.ruleList = this.ruleList;
          para = JSON.stringify(para);
          addEconomic(
            para,
            res => {
              this.$message({
                message: "",
                type: "success"
              });
              this.$refs["addForm"].resetFields();
              this.addFormVisible = false;
              this.getAlldata();
            },
            () => {
              this.$message({
                message: "",
                type: "error"
              });
              this.addFormVisible = false;
            }
          );
        }
      });
    },
    // 
    editSubmit() {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          if (this.addForm.ischeck == true) {
            this.addForm.ischeck = 1;
          } else {
            this.addForm.ischeck = 0;
          }
          var y = this.addForm.year;
          var n = y.slice(0, 4);
          this.addForm.year = n;
          let para = Object.assign({}, this.addForm);
          let { createtime, updatetime, ...others } = para;
          para = others;
          para.leaderList = this.leadsData;
          para.ruleList = this.ruleList;
          para = JSON.stringify(para);
          editIndex(
            para,
            res => {
              this.$message({
                message: "",
                type: "success"
              });
              this.addFormVisible = false;
              this.getAlldata();
            },
            () => {
              this.$message({
                message: "",
                type: "error"
              });
            }
          );
        }
      });
    }
  }
};
</script>


<style lang="scss">
.tableinfo .btn {
  color: #1890ff;
  background: transparent;
  border: 0;
  padding: 0 3px;
  margin: 0;
}
.managebox {
  .addrow {
    display: flex;
    align-items: stretch;
    fieldset {
      height: 100%;
    }
  }
  .boxcontent {
    padding: 20px;
    .day {
      .el-input__inner {
        width: 80px;
        border-radius: 4px;
      }
      .el-input-group__append {
        padding: 0 8px;
        background: #fff;
        border: 0;
        color: #222;
      }
      .el-input-group {
        display: flex;
        align-items: center;
      }
    }
    .flexbox {
      display: flex;
      .el-form-item {
        flex: 1;
      }
    }
  }
  .detail-check {
    .el-checkbox__input.is-disabled + span.el-checkbox__label {
      color: #222;
    }
    .el-checkbox__input.is-disabled.is-checked .el-checkbox__inner {
      background-color: #409eff;
      border-color: #409eff;
    }
    .el-checkbox__input.is-disabled .el-checkbox__inner {
      background: #fff;
    }
  }
  .el-checkbox__input.is-disabled.is-checked .el-checkbox__inner::after {
    color: #fff;
    border-color: #fff;
  }
  // .el-input__inner {
  //   width: 200px;
  // }
  // .el-date-editor.el-input,
  // .managebox .el-date-editor.el-input__inner {
  //   width: 100%;
  // }
  // .deptarea {
  //   .el-textarea__inner {
  //     width: 200px;
  //   }
  // }
  // .remark {
  //   display: block;
  //   .el-textarea__inner {
  //     width: 550px;
  //   }
  // }
  .yearbox {
    position: relative;
    width: 350px;
    margin-right: 10px;
    display: inline-block;
    .jiabtn {
      height: 36px;
      line-height: 36px;
      position: absolute;
      right: 0;
      top: 0;
      .icon {
        color: #666;
        font-size: 24px;
        display: inline-block;
        height: 40px;
        line-height: 40px;
        padding: 0 10px;
        cursor: pointer;
      }
      .awrap {
        position: absolute;
        left: 44px;
        top: 0;
        border: 1px solid #ccc;
        background: #fff;
        width: 250px;
        padding: 5px 10px 10px;
        z-index: 99;
        p {
          color: #666;
        }
        .el-textarea {
          width: 100%;
          margin-bottom: 10px;
        }
        .buttons {
          display: flex;
          justify-content: center;
          .el-button {
            padding: 8px 18px;
          }
        }
      }
    }
  }
}
.manage-index {
  .index-leader {
    margin-top: 20px;
    .addbtn {
      margin-bottom: 15px;
    }
    .boxcontent {
      padding: 20px;
    }
  }
  .task-dialog {
    .item-select {
      display: block;
      .el-select__tags {
        max-width: 100% !important;
        width: 100%;
        .el-input--suffix .el-input__inner {
          width: 100%;
        }
      }
    }
    .taskbox {
      display: flex;
      .day {
        .el-input__inner {
          width: 80px;
          border-radius: 4px;
        }
        .el-input-group__append {
          padding: 0 8px;
          background: #fff;
          border: 0;
          color: #333;
        }
      }
    }
    .taskbox2 {
      .el-input {
        width: 220px;
      }
    }
    .leabox {
      .el-form-item {
        width: 100%;
      }
    }
  }
  .task-dialog2 {
    .el-form--inline .el-form-item__content {
      width: 260px;
      .el-select {
        width: 100%;
      }
    }
    .leabox2 {
      .el-form-item__content {
        width: 634px;
      }
    }
  }
  .toolbar {
    .el-form--inline .ybox {
      margin-right: 5px;
    }
    .el-date-editor.el-input,
    .el-date-editor.el-input__inner {
      width: 150px;
    }
  }
}

.tableinfo {
  .flexbox {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .stateSelect {
    width: 50px;
    margin-left: 5px;
    .el-input__inner {
      background-color: transparent;
      padding: 0;
      border: none;
      height: 26px;
      line-height: 28px;
      font-size: 12px;
      color: #666;
    }
    .el-input__suffix {
      display: none;
    }
  }
}
</style>


